.live-pop-up {
    position: absolute;
    bottom: 10px;
    right: 70px;
    height: 50%;
    width: calc(100% - 83px);
    background: rgba(0, 0, 0, 0.7);
    border-radius: 4px;
    opacity: 0;

    -webkit-transition: all .3s ease;

    -o-transition: all .3s ease;

    transition: all .3s ease;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.live-pop-close {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 25px;
    height: 25px;
}

.live-pop-close path {
    fill: #f46036;
}

.live-pop-up-show {
    opacity: 1;
}

.live-pop-up h1, .live-pop-up h3 {
    margin: 10px;
}

.live-pop-up h1 {
    color: #5eeb5b;;
}

.live-pop-up h3 {
    color: white;
}


