.navigation-container {
    height: 50px;
    width: 100%;
    background: #443627;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.navigation-buttons {
    display: -ms-flexbox;
    display: flex;
    height: 35px;
    width: 35px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
}

.navigation-buttons svg {
    height: 100%;
    width: 100%;
}

.navigation-buttons svg path {
    fill: #5eeb5b;
}

.navigation-buttons svg circle {
    fill: #5eeb5b;
}


