.live-image-container {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
}

.live-image-container img {
    height: 100%;
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
}


