.livew-view-buttons {
    position: absolute;
    bottom: 10px;
    right: 0;
    width: 30px;
    /* height: 80px; */
    background: rgba(0, 0, 0, 0.7);
    padding: 10px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.livew-view-button {
    width: 100%;
    height: 30px;
    margin-top: 10px;
    position: relative;
}

.livew-view-button:first-child {
    margin: 0;
}

.livew-view-button svg {
    width: 100%;
    height: 100%;
}

.water-plant-button svg path {
    fill: aqua;
}

.water-plant-button svg rect {
    fill: aqua;
}

.thermometer-button svg path {
    fill: #f46036;
}

.thermometer-button svg circle {
    fill: #f46036;
}

.thermometer-button svg rect {
    fill: #f46036;
}

.humidity-button path {
    fill: #7474a5
}

.live-butons-indicator {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%) rotate(90deg);
        -ms-transform: translateY(-50%) rotate(90deg);
            transform: translateY(-50%) rotate(90deg);
    left: -31px;
    height: 70% !important;
    width: 70% !important;
    opacity: 0;

    -webkit-transition: all 0.3s ease;

    -o-transition: all 0.3s ease;

    transition: all 0.3s ease; 
}

.live-butons-indicator path {
    fill-opacity: 0.7;
    fill: black !important;
    stroke-opacity: 0.7 !important;
}


.live-butons-indicator-show {
    opacity: 1;
}


