.store{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
}

.store .first-container, .second-container{
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid rgba(0,0,0,0.4);
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
}

.store .plants-to-buy, .items-to-buy{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    overflow: auto;
}

.store .plants-to-buy-title, .items-to-buy-title{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-color: #443627;
    color: #61ff7e;
    height: 50px;
}

.store .plants-to-buy-title, .items-to-buy-title p{
    font-size: 30px;
}

.store .plants-to-buy-content, .items-to-buy-content{
    display: -ms-flexbox;
    display: flex;
    margin-left: 20%;
}
.store .simpleButton {
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    background: #5eeb5b;
    width: auto;
    border-radius: 0;
    padding: 0;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 0;
    cursor: pointer;
    -ms-flex-pack: center;
        justify-content: center;
    border-right:  1px solid rgba(0,0,0,0.4);
}

.store .simpleButton p {
    margin: 10px;
    color: white;
    cursor: pointer;
}

.store .add-funds{
    display: -ms-flexbox;
    display: flex;
    border-bottom: 1px solid rgba(0,0,0,0.4);
    height: 48px;
    font-weight: bold;
}

.store .add-funds .simpleButton{
    width: 50%;
}

.store .add-funds .coinsBalance{
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    font-weight: bold;
    background-color: rgba(255,255,255,0.5);
}


