.live-page {
    width: 100%;
    height: 100%;
    position: relative;
}

.live-view-title {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 10px);
    padding: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: white;
}

.live-view-title span {
    float: right;
}


